#one, #two, #three {
    height: 100vh;
    /* margin: 0px 20px 0px 20px; */
}

#one {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: -110px;
    /* margin-bottom: -160px; */
    margin-bottom: 85px;
}

#left > * {
    padding-bottom: 20px;
    letter-spacing: 3px;
    max-width: 420px;
}

#left > h1 {
    font-size: 46px;
    /* color: #53C1DE; */
    color: rgb(128, 128, 128);
    font-weight: 400;
}

#h4 {
    color: gray;
    font-size: 18px;
    font-style: italic;
    font-weight: 200;
}

#socials {
    display: flex;
    flex-direction: column;
    display: grid;
    place-items: center;
}

a {
    color: black;
}

#links {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 220px;
    margin-bottom: 20px;
}

.MuiSvgIcon-root:hover {
    transform: scale(1.2) !important;
    transition: 1s all ease;
}

.MuiSvgIcon-root {
    margin-top: 20px;
    font-size: 50px !important;
}

#resume {
    cursor: pointer;
    background-color: #9FC9A3;
    color: white;
    text-decoration: none;
    padding: 20px 80px;
    border-radius: 5px;
}

#resume:hover {
    color: #9FC9A3;
    background-color: white;
    transition: 1s all ease;
}

/* second section */
#two {
    margin-top: -250px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    /* align-items: center; */
}

.inner-two {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: -350px;
}

#right {
    /* display: grid;
    place-items: center;
    width: 100%; */
}

#two > h1 {
    font-size: 42px;
    font-weight: 500;
}

.two {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}

img:hover {
    transform: scale(1.1);
    cursor: pointer;
    background-color: transparent;
    transition: 1s all ease;
}

.stack {
    display: flex;
    align-items: center;
    /* max-width: 300px; */
    max-width: 600px;
    flex-wrap: row column;
    justify-content: space-around;
}

#right {
    flex: 0.5;
    padding: 10px;
    /* margin-top: -100px;
    margin-left: 30px; */
}

#right > h1 {
    display: grid;
    place-items: center;
    font-size: 40px;
}

#img {
    flex: 0.5;
    cursor: initial;
    width: 600px;
    margin-top: 100px;
}

#right-h1 {
    font-weight: 200;
    font-size: 36px;
}

#bullet-pnts {
    display: flex;
    flex-direction: column;
    /* margin-left: 40px; */
    padding: 20px;
    color: grey;
}

#pnt {
    list-style: circle;
}

.stack > img {
    padding: 10px;
}

/* third section */
#third {
    display: flex;
    margin-top: 50px;
}

#cloud-right {
    display: flex;
    flex-direction: column;
}

.tools {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-evenly;
}

#fourth {
    margin-top: 100px;
    display: flex;
}

#h1 {
    width: 100%;
    font-size: 42px;
    display: flex;
    font-weight: 200;
    display: grid;
    place-items: center;
}

#design-tools {
    display: flex;
    flex-direction: column;
    width: 100%;
}

#two, #third, #fourth {
    display: flex;
    align-items: center;
}

/* media queries */
/* 965 */
@media only screen and (max-width: 950px) {
    #one {
        margin-top: 5px;
        flex-direction: column-reverse;
    }

    img {
        width: 85%;
    }

    .inner-two {
        flex-direction: column;
    }
    #two {
        height: 100%;
        width: 100%;
    }

    .stack {
        width: 400px;
    }

    #bullet-pnts {
        max-width: 450px;
    }

    #right > h1 {
        margin-top: 30px;
        font-size: 42px;
        max-width: 450px;
    }

}